*,
::before,
::after {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: rgba(210, 165, 30, 0.6);
}
header img {
  width: 90px;
}
header .subheader_wrap {
  text-align: center;
}
.home,
.form_wrap > *,
.btn_wrap,
label {
  display: block;
  width: 70%;
  margin: auto;
}
label > * {
  display: block;
  width: 100%;
}
label input,
label select {
  font-size: 15px;
  padding: 4px 0 7px 5px;
  transition-duration: 200ms;
}
label input:hover,
label select:hover {
  border-color: rgb(169, 169, 169);
}
input::placeholder {
  color: rgb(0, 0, 0);
}
.btn_wrap button {
  margin-right: 1rem;
}
.home .user_action {
  text-align: right;
}
button {
  border-width: 4px;
  border-style: solid;
  border-radius: 5px;
  border-color: rgb(169, 169, 169);
  padding: 5px;
  margin: 1rem 0;
  cursor: pointer;
  transition-duration: 200ms;
}
button:hover {
  background-color: rgb(169, 169, 169);
}
span {
  margin: 5px;
}
.secondary {
  font-size: 0.8rem;
  color: rgb(169, 169, 169);
}
